import React, { useState } from "react";
import { DatePicker, Space } from "antd";
import dayjs from "dayjs";
import customParseFormat from "dayjs/plugin/customParseFormat";
import weekOfYear from "dayjs/plugin/weekOfYear";

dayjs.extend(customParseFormat);
dayjs.extend(weekOfYear); // Extend dayjs to use week of year

const WeeklyMonthlyPicker = ({ placeholder, style, handleChnage, value }) => {
  const [format, setFormat] = useState("");
  const [activePicker, setActivePicker] = useState("weekly");
  // Function to format the selected month
  const formatMonth = (value) => {
    const start = dayjs(value).startOf("month").format("YYYY-MM-DD"); // Get start date of month
    const end = dayjs(value).endOf("month").format("YYYY-MM-DD"); // Get end date of month
    return {
      month: dayjs(value).format("MMMM-YY"),
      start,
      end,
    }; // e.g., { month: "October", start: "2024-10-01", end: "2024-10-31" }
  };

  // Function to format the selected week
  const formatWeek = (value) => {
    const start = dayjs(value).startOf("week").format("YYYY-MM-DD"); // Get start date of week
    const end = dayjs(value).endOf("week").format("YYYY-MM-DD"); // Get end date of week
    return {
      week: dayjs(value).week(),
      start,
      end,
    }; // e.g., { week: 42, start: "2024-10-15", end: "2024-10-21" }
  };

  // Function to display the formatted string in the DatePicker
  const displayFormat = (value) => {
    if (format === "month") {
      return formatMonth(value).month; // Return month name for display
    } else {
      return `Week ${formatWeek(value).week}`; // Return week number for display
    }
  };

  return (
    <DatePicker
      style={style}
      placeholder={placeholder}
      size="large"
      format={displayFormat} // Use the display format function
      picker={format === "month" ? "month" : "week"}
      onChange={(value) => {
        if (value) {
          // Check if the value is valid before formatting
          const formatted =
            format === "month" ? formatMonth(value) : formatWeek(value);
          handleChnage(formatted);
        } else {
          // If the value is null (cleared), pass an empty string
          handleChnage("");
        }
      }}
      renderExtraFooter={() => {
        return (
          <div className="flex items-center gap-2 w-full justify-center">
            <div className="p-1 ">
              <div
                onClick={() => {
                  setFormat("week");
                  setActivePicker("weekly");
                }}
                className={`cursor-pointer font-medium text-text-primary ${
                  activePicker === "weekly"
                    ? "bg-text-primary text-white"
                    : "bg-white"
                } h-8 px-3 flex items-center justify-center rounded`}
              >
                Weekly
              </div>
            </div>
            <div className="p-3 ">
              <div
                onClick={() => {
                  setFormat("month");
                  setActivePicker("monthly");
                }}
                className={`cursor-pointer font-medium text-text-primary ${
                  activePicker === "monthly"
                    ? "bg-text-primary text-white"
                    : "bg-white"
                } h-8 px-3 flex items-center justify-center rounded`}
              >
                Monthly
              </div>
            </div>
          </div>
        );
      }}
    />
  );
};

export default WeeklyMonthlyPicker;
